.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/*DaahBoard css*/
/* components/Dashboard.css */
.dashboard-container {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.notes-section {
  flex: 2;
  margin-right: 20px;
}

.note-card {
  border: 1px solid #ccc;
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  background-color: #f8f8f8;
}

.create-note-form {
  flex: 1;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 5px;
  background-color: #f1f1f1;
}

.create-note-form form {
  display: flex;
  flex-direction: column;
}

.create-note-form input,
.create-note-form textarea {
  margin-bottom: 10px;
  padding: 8px;
  font-size: 14px;
}

.create-note-form button {
  padding: 10px;
  font-size: 16px;
  background-color: #0077ff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.create-note-form button:hover {
  background-color: #005fd1;
}
